body {
  margin: 0;
  overflow: hidden;
  user-select: none;
}

.app-container {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100vw;
  height: 100vh;
  outline: none;
}

.overlay-text {
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 600;
  font-size: 40px;
  line-height: 30vh;
  padding-top: 70vh;
  text-align: center;
}

.close-button {
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 600;
  position: fixed;
  width: 120px;
  height: 40px;
  left: calc(50vw - 60px);
  top: 40px;
}